.profile__user-name {
  font-weight: 500;
  font-size: 27px;
  line-height: 33px;
  margin: 0;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 200px;
}
  
@media screen and (min-width: 768px){
  .profile__user-name {
    font-size: 42px;
    line-height: 48px;
    max-width: 300px;
  }
}